body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

td, th {
  border: 1px solid #f7f7f7;
  text-align: left;
  padding: 8px;
  background: #f9f9f9;
  color: #281654;
}

nav {
  width: 100%;
  display: flex;
  margin: 0;
  background: #f9f9f9;
  box-shadow: 0 0 10px 0 rgb(0 0 0 / 24%);
  padding: 10px;
  position: fixed;
  top: 0px;
  z-index: 1;
}

nav ul {
  width: auto;
  margin: 0;
  padding: 0px;
}

nav ul li {
  display: inline-flex;
  padding: 10px;
  min-width: 85px;
  font-size: 15px;
  border: none;
  margin: auto 5px;
  border-radius: 4px;
  background: white;
}

nav ul li:hover {
  box-shadow: 0px 5px 15px 0px #00000070;
  transition: 0.2s ease-in-out;
  background: rgb(2,0,36);
  background: linear-gradient(
90deg, rgba(2,0,36,1) 0%, rgba(209,209,209,1) 0%, rgba(232,232,232,1) 100%);
}

nav ul li:hover a {
  color:black;
}

nav ul li a {
  color: #707070;
  text-decoration: none;
  font-weight: bold;
  width: 100%;
  text-align: center;
}

button.Default-Anchor-Button {
  padding: 12px;
  min-width: 100px;
  color: #707070;
  font-weight: bold;
  font-size: 10px;
  border-radius: 2px;
  border: none;
  border-bottom: 2px solid #d9d9d9;
  background: #ffffff;
}

button.Default-Anchor-Button.Selected {
  border-bottom: 2px solid #0073c5;
}

button.Default-Anchor-Button:hover {
  cursor: pointer;
  color: black;
  box-shadow: 0px 5px 15px 0px #0000002b;
  transition: 0.2s ease-in-out;
  background: rgb(226 226 226);
  background: linear-gradient(rgba(2,0,36,1) 0%, rgb(255 255 255) 0%, rgb(226 226 226) 100%);
}

button.Default-Action-Button {
  padding: 10px;
  min-width: 115px;
  color: white;
  font-weight: bold;
  font-size: 12px;
  border-radius: 0;
  border: 2px solid white;
  background: rgb(225,1,255);
  background: linear-gradient(90deg, rgb(52 1 255) 0%, rgb(102 0 82) 0%, rgb(50 0 58) 100%);
}

button.Default-Action-Button:hover {
  cursor:pointer;
  color:black;
  box-shadow: 0px 5px 15px 0px #00000070;
  transition: 0.2s ease-in-out;
  background: rgb(2,0,36);
  background: linear-gradient( 
90deg, rgba(2,0,36,1) 0%, rgba(209,209,209,1) 0%, rgba(232,232,232,1) 100%);
}

.Default-Form-Theme {
  font-size: 2.0rem;
  padding: 0px 25px 25px 25px;
  background: #2e0f7a;
  margin: 200px auto 50px auto;
  min-width: 80%;
  min-height: 250px;
  border-radius: 10px;
  box-shadow: 10px 30px 40px 5px #00000069;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  backdrop-filter: blur(10px);
}

.Default-Form-Theme {
  padding: 15px;
  width: 70%;
  margin: auto;
  border: 1px solid green;
}

.Default-Form-Theme {
  font-size: 2.0rem;
  padding: 25px;
  background: #2e0f7a;
  margin: 300px auto 50px auto;
  min-width: 80%;
  min-height: 250px;
  border-radius: 50px;
  box-shadow: 10px 30px 40px 5px #00000069;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  backdrop-filter: blur(10px);
}

.Default-Input-Wrapper label {
  font-size: 20px;
  font-weight: 300;
  padding: 20px;
}

.Default-Input-Wrapper input.Default-Input {
  padding: 10px;
  background: #ffffff;
  border: 2px solid #3b013f;
  border-radius: 5px;
}

.Small .Option-Wrapper label {
  font-size: 15px;
  margin-right: 15px;
}
.Option-Wrapper, .Input-Wrapper {
  background: #f9f9f9;
  padding: 1%;
  color: black;
  text-align: center;
  border-bottom: 2px solid #d9d9d9;
}
.Option-Wrapper label, .Input-Wrapper label {
  font-size: 15px;
}

button.Default-Anchor-Button.Special {
  background: #0085c5;
  color: white;
}

.Content-Wrapper {
  font-size: 60px;
  text-align: center;
  color: #0085c5;
  background: var(--bgLight-1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Noto Sans",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: visible!important;
  width: 100%;
  height: 100%;
  border-radius: 2px;
  box-shadow: 3px 2px 30px 2px #00000024;
  transition: transform 1s;
  position: relative;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
}
.Component-Title {
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 20px;
  color: #3e94cd;
  font-weight: bold;
}
.Timer-List {
  position: relative;
  top: -14px;
  left: -6%;
  align-items: center;
  margin: auto;
  padding: 0;
  display: table;
  width: 112%;
}
.Timer-Goal-Wrapper img {
  width: 100%;
  max-width: 380px;
}

.Rounds-Preview {
  font-size: 20px;
  font-weight: bold;
  color: #3e94cd;
}
.Title-Wrapper{
  font-family: inherit;
  padding-top:15px;
  font-size:15px;
}
.Timer-List.inactive {
  pointer-events: none;
  opacity: 0.5;
}
.Workflow-Wrapper {
  padding-top: 0;
  height: 100%;
  overflow-y: scroll;
}
table.sc-eCstlR {
  width: 75%;
  display: table;
  margin: auto;
}
.Timer-Tittle {
  font-size: 25px;
  padding: 10px;
  font-weight: 300;
}
.Default-Container {
  Width: 100%;
}
.Float-Right {
  float: right;
}
.Float-Left {
  float: left;
}
.Hidden {
  display: none;
}
.Preview.Small {
  font-size: 15px;
  color: #858585;
}